.sidebar {
    position: fixed;
    background-color: white;
    opacity: 1;
    height: 100vh;
    width: 300px;
    padding: 20px;
    top: 0;
    left: -300px;
    z-index: 10;
    transition: left 300ms;
}

.sidebar.open {
    left: 0;
    box-shadow: 9px 1px 18px -5px rgba(0,0,0,0.75);
    -webkit-box-shadow: 9px 1px 18px -5px rgba(0,0,0,0.75);
    -moz-box-shadow: 9px 1px 18px -5px rgba(0,0,0,0.75);
}

.background {
    background-color: rgba(44, 46, 44, 0.4);
    opacity: 0.5;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.nav a {
}

.nav a:hover {
}

.nav a.active {
    color: red;
}
.close {
    position: absolute;
    right: 10px;
    top: 10px;
    border: none;
    background-color: transparent;
}

.close img {
}
